import {
  GET_TECHS,
  ADD_TECH,
  DELETE_TECH,
  TECHS_ERROR,
  SET_LOADING,
} from '../actions/type';

export const setLoading = () => {
  return {
    type: SET_LOADING,
  };
};

export const getTechs = () => async (dispatch) => {
  try {
    const res = await fetch('./techs');
    const data = await res.json();

    dispatch({
      type: GET_TECHS,
      payload: data,
    });
  } catch (err) {
    console.log(err.response);
    dispatch({
      type: TECHS_ERROR,
      payload: err.response,
    });
  }
};

export const addTechs = (tech) => async (dispatch) => {
  try {
    setLoading();
    const res = await fetch('./techs', {
      method: 'POST',
      body: JSON.stringify(tech),
      headers: {
        'Content-Type': 'application/json',
      },
    });
    const data = await res.json();
    dispatch({
      type: ADD_TECH,
      payload: data,
    });
  } catch (err) {
    console.log(err.response);
    dispatch({
      type: TECHS_ERROR,
      payload: err.response,
    });
  }
};

export const delTechs = (id) => async (dispatch) => {
  console.log('DELETE');
  try {
    setLoading();
    await fetch(`./techs/${id}`, {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
      },
    });
    dispatch({
      type: DELETE_TECH,
      payload: id,
    });
  } catch (err) {
    console.log(err.response);
    dispatch({
      type: TECHS_ERROR,
      payload: err.response,
    });
  }
};
