import React, { Fragment } from 'react';
import Moment from 'react-moment';
import { delLogs, setCurrent } from '../../actions/logAction';
import { connect } from 'react-redux';

const LogItem = (props) => {
  const { log, delLogs, setCurrent } = props;

  return (
    <Fragment>
      <li className="collection-item">
        <a
          href="#edit-log-modal"
          className={`modal-trigger ${
            log.attention ? 'red-text' : 'blue-text'
          }`}
          onClick={() => {
            setCurrent(log);
          }}
        >
          {log.attention && (
            <i className="large material-icons" style={{ fontSize: '20px' }}>
              announcement
            </i>
          )}

          {log.message}
        </a>
        <br></br>
        <span className="grey-text">
          <span className="black-text">ID #{log.id}</span> last update by{' '}
          <span
            className="black-text text-darken-2"
            style={{ fontWeight: '600' }}
          >
            {log.tech} on{' '}
            <Moment format="MMMM Do YYYY, h:mm:ss a">{log.date}</Moment>
          </span>
        </span>

        <a
          href="#!"
          className="secondary-content"
          onClick={() => {
            delLogs(log.id);
          }}
        >
          <i className="material-icons grey-text">delete</i>
        </a>
      </li>
    </Fragment>
  );
};

export default connect(null, { delLogs, setCurrent })(LogItem);
