import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { getTechs } from '../../actions/techAction';

const TechOptions = ({ tech: { tech, loading }, getTechs }) => {
  useEffect(() => {
    getTechs();
  }, []);

  if (tech === null || loading) {
    return <option>Loading...</option>;
  }
  if (tech !== null && tech.length === 0) {
    return <option>There is none technician available.</option>;
  }
  return tech.map((eachTech) => (
    <option
      key={eachTech.id}
      value={`${eachTech.firstName} ${eachTech.lastName}`}
    >
      {eachTech.firstName} {eachTech.lastName}
    </option>
  ));
};
const mapStateToProps = (state) => ({
  tech: state.tech,
});

export default connect(mapStateToProps, { getTechs })(TechOptions);
