import '../layout/materialize-custom.css';
import './materialize-custom.css';
import React, { Fragment, useState } from 'react';
import M from 'materialize-css/dist/js/materialize.min.js';
import { connect } from 'react-redux';
import { addLogs } from '../../actions/logAction';
import TechOptions from '../tech/TechOptions';

const AddLogModal = ({ addLogs }) => {
  const [msg, setMsg] = useState('');
  const [attention, setAttention] = useState(false);
  const [tech, setTech] = useState('');

  const onChagne = (event) => {
    if (event.target.name === 'msg') {
      setMsg(event.target.value);
    } else if (event.target.name === 'attention') {
      setAttention(!attention);
    } else if (event.target.name === 'tech') {
      setTech(event.target.value);
    }
  };

  const onSubmit = (event) => {
    if (msg === '' || tech === '') {
      M.toast({ html: 'Please fill in all required fields' });
      return;
    } else {
      const newLog = {
        message: msg,
        attention,
        tech,
        date: new Date(),
      };
      addLogs(newLog);
      M.toast({ html: `Log added by ${tech} !` });
      setTech('');
      setMsg('');
      setAttention(false);
    }
  };

  return (
    <Fragment>
      <div id="add-log-modal" className="modal modal-custom">
        <div className="modal-content">
          <h4 className="blue-text text-darken-2">Enter System Log</h4>
          {/** Message */}
          <div className="row">
            <div className="input-field">
              <input type="text" name="msg" value={msg} onChange={onChagne} />
              <label htmlFor="msg" className="active blue-text text-darken-2">
                Log Message
              </label>
            </div>
          </div>
          {/** Select */}
          <div className="row">
            <div className="input-field">
              <select
                name="tech"
                value={tech}
                className="browser-default blue-text text-darken-2"
                onChange={onChagne}
              >
                <option value="" disabled>
                  Select Technician
                </option>
                <TechOptions />
              </select>
            </div>
          </div>
          {/** Attention */}
          <div className="row">
            <div className="input-field">
              <p>
                <label>
                  <input
                    name="attention"
                    type="checkbox"
                    className="filled-in blue darken-1"
                    checked={attention}
                    value={attention}
                    onChange={onChagne}
                  />
                  <span className="blue-text text-darken-2">
                    Need attention ?
                  </span>
                </label>
              </p>
            </div>
          </div>
          {/** Modal Footer */}
          <div className="modal-footer">
            <a
              href="#!"
              onClick={onSubmit}
              className="modal-close waves-effect waves-blue btn blue-btn blue darken-1"
            >
              Enter
            </a>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default connect(null, { addLogs })(AddLogModal);
