import '../layout/materialize-custom.css';
import React, { Fragment, useState } from 'react';
import M from 'materialize-css/dist/js/materialize.min.js';
import './custom-component.css';
import { connect } from 'react-redux';
import { addTechs } from '../../actions/techAction';

const TechModal = ({ addTechs }) => {
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');

  const onChagne = (event) => {
    if (event.target.name === 'firstName') {
      setFirstName(event.target.value);
    } else if (event.target.name === 'lastName') {
      setLastName(event.target.value);
    }
  };

  const onSubmit = (event) => {
    if (lastName === '' || firstName === '') {
      M.toast({ html: 'Please fill in all required fields' });
    } else {
      setFirstName('');
      setLastName('');
      const newTech = {
        firstName: firstName,
        lastName: lastName,
      };
      addTechs(newTech);
      M.toast({
        html: `${firstName} ${lastName} technician have been added into database`,
      });
    }
  };

  return (
    <Fragment>
      <div id="tech-modal" className="modal  modal-custom">
        <div className="modal-content modal-content-custom">
          <h4 className="grey-text darken-4">Add New Technician</h4>
          {/** First Name */}
          <div className="row">
            <div className="input-field">
              <input
                type="text"
                name="firstName"
                value={firstName}
                onChange={onChagne}
              />
              <label htmlFor="firstName" className="active grey-text darken-4">
                First Name
              </label>
            </div>
          </div>
          {/** Last Name */}
          <div className="row">
            <div className="input-field">
              <input
                type="text"
                name="lastName"
                value={lastName}
                onChange={onChagne}
              />
              <label htmlFor="msg" className="active grey-text darken-4">
                Last Name
              </label>
            </div>
          </div>
        </div>
        {/** Modal Footer */}
        <div className="modal-footer">
          <a
            href="#!"
            onClick={onSubmit}
            className="modal-close waves-effect waves-red btn red-btn grey darken-4 btn-custom"
          >
            Add Technician
          </a>
        </div>
      </div>
    </Fragment>
  );
};

export default connect(null, { addTechs })(TechModal);
