import React, { useEffect, Fragment } from 'react';
import 'materialize-css/dist/css/materialize.min.css';
import M from 'materialize-css/dist/js/materialize.min.js';

import SearchBar from '../src/component/layout/SearchBar';
import Logs from '../src/component/logs/Logs';
import Footer from '../src/component/layout/Footer';
import AddBtn from '../src/component/layout/AddBtn';
import AddLogModal from '../src/component/layout/AddLogModal';
import EditLogModal from '../src/component/layout/EditLogModal';
import TechModal from '../src/component/tech/AddTechModal';
import ShowTechModal from '../src/component/tech/ShowTechModal';
import { Provider } from 'react-redux';
import store from '../src/Store';

function App() {
  useEffect(() => {
    /** Initialize M js */
    M.AutoInit();
  });

  return (
    <Provider store={store}>
      <Fragment>
        <SearchBar />
        <AddLogModal />
        <EditLogModal />
        <TechModal />
        <ShowTechModal />
        <Logs />
        <AddBtn />
        <Footer />
      </Fragment>
    </Provider>
  );
}

export default App;
