import React, { Fragment, useEffect } from 'react';
import TechItem from './TechItem';
import '../layout/materialize-custom.css';
import './custom-component.css';
import PreLoader from '../layout/PreLoader';
import { getTechs } from '../../actions/techAction';
import { connect } from 'react-redux';

const ShowTechModal = ({ tech: { tech }, getTechs }) => {
  useEffect(() => {
    getTechs();
    // eslint-disable-next-line
  }, []);

  const deleteTech = (event) => {};

  if (tech === null) {
    return (
      <Fragment>
        <div id="tech-list-modal" className="modal  modal-tech-custom">
          <div className="modal-content modal-content-custom">
            <h4 className="green-text darken-4">Available Technician</h4>
            <PreLoader />
          </div>
          {/** Modal Footer */}
          <div className="modal-footer">
            <a
              href="#!"
              onClick={(e) => {
                console.log('Close Modal');
              }}
              className="modal-close waves-effect waves-red btn green-btn green darken-4 btn-custom"
            >
              Close
            </a>
          </div>
        </div>
      </Fragment>
    );
  }

  return (
    <Fragment>
      <div id="tech-list-modal" className="modal  modal-tech-custom">
        <div className="modal-content modal-content-custom">
          <h4 className="green-text darken-4">Available Technician</h4>
          <ul className="collection">
            {/**List of Available Technician*/}
            {tech.map((eachTech) => (
              <TechItem key={eachTech.id} tech={eachTech} />
            ))}
          </ul>
        </div>
        {/** Modal Footer */}
        <div className="modal-footer">
          <a
            href="#!"
            onClick={() => console.log('Close')}
            className="modal-close waves-effect waves-red btn green-btn green darken-4 btn-custom"
          >
            Close
          </a>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  tech: state.tech,
});

export default connect(mapStateToProps, { getTechs })(ShowTechModal);
