import '../layout/materialize-custom.css';
import React, { Fragment, useState, useEffect, createRef } from 'react';
import M from 'materialize-css/dist/js/materialize.min.js';
import { connect } from 'react-redux';
import { editLogs, clearCurrent } from '../../actions/logAction';
import TechOptions from '../tech/TechOptions';

const EditLogModal = ({ log: { current }, editLogs, clearCurrent }) => {
  const [msg, setMsg] = useState('');
  const [attention, setAttention] = useState(false);
  const [tech, setTech] = useState('');
  const msgInput = createRef();

  useEffect(() => {
    if (current !== null) {
      msgInput.current.focus();
      setMsg(current.message);
      setAttention(current.attention);
      setTech(current.tech);
    }
  }, [current]);

  const onChagne = (event) => {
    if (event.target.name === 'msg') {
      setMsg(event.target.value);
    } else if (event.target.name === 'attention') {
      setAttention(!attention);
    } else if (event.target.name === 'tech') {
      setTech(event.target.value);
    }
  };

  const onSubmit = (event) => {
    event.preventDefault();
    if (msg === '' || tech === '') {
      M.toast({ html: 'Please fill in all required fields' });
    } else {
      const newLog = {
        message: msg,
        attention,
        tech,
        date: new Date(),
        id: current.id,
      };
      editLogs(newLog);
      setMsg('');
      setTech('');
      setAttention(false);
      clearCurrent();
    }
  };

  return (
    <Fragment>
      <div id="edit-log-modal" className="modal modal-custom">
        <div className="modal-content">
          <h4 className="red-text text-darken-2">Edit System Log</h4>
          {/** Message */}
          <div className="row">
            <div className="input-field">
              <input
                type="text"
                name="msg"
                value={msg}
                onChange={onChagne}
                ref={msgInput}
              />
              <label htmlFor="msg" className="active red-text text-darken-2">
                Log Message
              </label>
            </div>
          </div>
          {/** Select */}
          <div className="row">
            <div className="input-field">
              <select
                name="tech"
                value={tech}
                className="browser-default red-text text-darken-2"
                onChange={onChagne}
              >
                <option value="" disabled>
                  Select Technician
                </option>
                <TechOptions />
              </select>
            </div>
          </div>
          {/** Attention */}
          <div className="row">
            <div className="input-field">
              <p>
                <label>
                  <input
                    name="attention"
                    type="checkbox"
                    className="filled-in red darken-1"
                    checked={attention}
                    value={attention}
                    onChange={onChagne}
                  />
                  <span className="red-text text-darken-2">
                    Need attention ?
                  </span>
                </label>
              </p>
            </div>
          </div>
          {/** Modal Footer */}
          <div className="modal-footer">
            <a
              href="#!"
              onClick={onSubmit}
              className="modal-close waves-effect waves-red btn red-btn red darken-1"
            >
              Enter
            </a>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  log: state.log,
});

export default connect(mapStateToProps, { editLogs, clearCurrent })(
  EditLogModal
);
