import React, { Fragment } from 'react';
import M from 'materialize-css/dist/js/materialize.min.js';
import { delTechs } from '../../actions/techAction';
import { connect } from 'react-redux';

const TechItem = (props) => {
  const lastName = props.tech.lastName;
  const firstName = props.tech.firstName;
  const id = props.tech.id;
  const delTechs = props.delTechs;
  const onClick = async () => {
    M.toast({ html: 'Technician information deleted' });
    delTechs(id);
  };

  return (
    <Fragment>
      <li className="collection-item">
        <i className="small material-icons green-text">work</i>
        <span style={{ fontWeight: '600' }}>
          {firstName} {lastName}
        </span>
        <a href="#!" className="secondary-content" onClick={onClick}>
          <i className="material-icons black-text">delete</i>
        </a>
      </li>
    </Fragment>
  );
};

export default connect(null, { delTechs })(TechItem);
