import {
  GET_TECHS,
  ADD_TECH,
  DELETE_TECH,
  TECHS_ERROR,
  SET_LOADING,
} from '../actions/type';

const initalState = {
  tech: null,
  current: null,
  loading: false,
  error: null,
};

export default (state = initalState, action) => {
  switch (action.type) {
    case SET_LOADING:
      return {
        ...state,
        loading: true,
      };
    case GET_TECHS:
      return {
        ...state,
        tech: action.payload,
        loading: false,
      };
    case TECHS_ERROR: {
      return {
        ...state,
        error: action.payload,
      };
    }
    case ADD_TECH: {
      return {
        ...state,
        tech: [...state.tech, action.payload],
        loading: false,
      };
    }
    case DELETE_TECH: {
      return {
        ...state,
        tech: state.tech.filter((eachTech) => eachTech.id !== action.payload),
        loading: false,
      };
    }
    default:
      return {
        ...state,
      };
  }
};
